<template>
    <el-dialog width="50%"
               title="可视化发布"
               :visible.sync="state.visible"
               @open="onOpen">
        <el-form :model="config.dataSource"
                 size="mini"
                 label-width="60px"
                 ref="dataSourceForm">
            <el-form-item label="发布">
                <el-switch v-model="config.state"
                           inactive-color="#a0a0a0"
                           :active-value="1"
                           :inactive-value="0"
                           @change="publish"></el-switch>
            </el-form-item>
            <el-form-item label="网址">
                <el-input id="publish-url"
                          type="textarea"
                          v-model="url"
                          size="medium"
                          rows="2"
                          readonly></el-input>
            </el-form-item>
            <el-form-item label=" ">
                <el-button data-clipboard-action="copy"
                           data-clipboard-target="#publish-url"
                           type="primary"
                           size="mini"
                           class="clipboard"
                           @click="copyUrl">复制网址
                </el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</template>

<script>
    import Clipboard from 'clipboard'
    import API from '../../config/api'
    import CONST from '../../config/const'
    import _ from 'lodash'

    export default {
        name: "Publisher",
        props: ['config', 'state'],
        data() {
            return {
                clipboard: null,
                url: ''
            }
        },
        methods: {
            /**
             * 打开时初始化复制按钮
             */
            onOpen() {
                if (!this.clipboard) {
                    this.clipboard = new Clipboard('.clipboard');
                }
            },
            /**
             * 复制网址
             */
            copyUrl() {
                let me = this
                this.clipboard.on('success', function () {
                    me.$message({
                        message: '复制成功！',
                        type: 'success'
                    });
                });
                this.clipboard.on('error', function () {
                    me.$message({
                        message: '复制失败，请手动选择复制！',
                        type: 'error'
                    });
                });
            },
            publish(val) {
                this.config.state = val
                let config = this.formatConfigParam(this.config)
                this.ajax({
                    url: API.SCREEN.PUBLISH,
                    method: CONST.AJAX.POST,
                    data: config,
                }).then(response => {
                    this.config.id = response.data.id
                    this.url = ''
                    if (val) {
                        this.url = API.WEB_URL + '/presentation?id=' + response.data.id
                    }
                })
            },
            /**
             * 将可视化配置转为参数
             */
            formatConfigParam(config) {
                let param = _.cloneDeep(config)
                for (let key in param) {
                    let value = param[key]
                    if (_.isObject(value)) {
                        param[key] = JSON.stringify(value)
                    }
                }
                return param
            },
        }
    }
</script>

